$totalCols: 16;
$twelveCols: 12;

.grid {
    position: relative;
    @include clearfix;

    .row {
        @include clearfix;
        @media screen and (min-width : 768px) {
            margin-bottom: 1em;
        }
    }

    [class*="col-"] {
        float: left;
        width: 100%;
        min-height: 1px;
        position: relative;
    }
    @for $i from 1 through $totalCols {
        .col-#{$i}-#{$totalCols} {
            width: 100%;
            @media screen and (min-width: 600px) {
                width: 50%;
            }
            @media screen and (min-width: 768px) {
                width: 100% / $totalCols * $i;
            }
        }
    }
    @for $i from 1 through $twelveCols {
        .col-#{$i}-#{$twelveCols} {
            width: 100%;
            @media screen and (min-width: 600px) {
                width: 50%;
            }
            @media screen and (min-width: 768px) {
                width: 100% / $twelveCols * $i;
            }
        }
    }
}
